import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/html/Main'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      children: [
          {
            path: 'bb-rules',
            name: 'rules',
            component: resolve => {
                require(['@/html/bb-rules.vue'], resolve);
            }
          },
          {
            path: 'bb-previous',
            name: 'previous',
            component: resolve => {
                require(['@/html/bb-previous.vue'], resolve);
            }
          },
          {
            path: 'bb-news',
            name: 'news',
            component: resolve => {
                require(['@/html/bb-news.vue'], resolve);
            }
          },
          {
            path: 'bb-announcement',
            name: 'announcement',
            component: resolve => {
                require(['@/html/bb-announcement.vue'], resolve);
            }
          },
          {
            path: 'bb-announcement-detail',
            name: 'announcementDetail',
            component: resolve => {
                require(['@/html/bb-announcement-detail.vue'], resolve);
            }
          },
          {
            path: 'bb-video',
            name: 'video',
            component: resolve => {
                require(['@/html/bb-video.vue'], resolve);
            }
          },
          {
            path: 'bb-video-detail',
            name: 'videoDetail',
            component: resolve => {
                require(['@/html/bb-video-detail.vue'], resolve);
            }
          },
          {
            path: 'bb-dynamics',
            name: 'dynamics',
            component: resolve => {
                require(['@/html/bb-dynamics.vue'], resolve);
            }
          },
          {
            path: 'bb-dynamics-detail',
            name: 'dynamicsDetail',
            component: resolve => {
                require(['@/html/bb-dynamics-detail.vue'], resolve);
            }
          },
          {
              path: '/',
              redirect: 'bb-news'
          }
      ]
    }
  ]
})



